
const initialState = {
  GetBobotKecukupans: [],
  SetBobotKondisi: [],
  GetBobotKetersediaan: [],
  modalKecukupan: false,
  modalKetersediaan: false,
  modalKondisi: false,
  setFormKecukupan: {
    Id: 0,
    Nama: "",
    Bobot: 0,
  },
  setFormKetersediaan: {
    Id: 0,
    Nama: "",
    Bobot: 0,
  },
  setFormKondisi: {
    Id: 0,
    Nama: "",
    Bobot: 0,
  },
};


const masterBobotReducer = (state = initialState, action) =>{
    switch (action.type) {
      case "GET_LIST_KECUKUPAN":
        return { ...state, GetBobotKecukupans: action.payload };
      case "GET_LIST_KETERSEDIAAN":
        return { ...state, GetBobotKetersediaan: action.payload };
      case "GET_LIST_KONDISI":
        return { ...state, SetBobotKondisi: action.payload };
      case "MODAL_KECUKUPAN":
        return { ...state, modalKecukupan: action.payload };
      case "MODAL_KETERSEDIAAN":
        return { ...state, modalKetersediaan: action.payload };
      case "MODAL_KONDISI":
        return { ...state, modalKondisi: action.payload };
      case "RESET_FORM_KECUKUPAN":
        return { ...state, setFormKecukupan: initialState.setFormKecukupan };
      case "RESET_FORM_KETERSEDIAAN":
        return {
          ...state,
          setFormKetersediaan: initialState.setFormKetersediaan,
        };
      case "RESET_FORM_KONDISI":
        return {
          ...state,
          setFormKondisi: initialState.setFormKondisi,
        };
      case "ADD_KECUKUPAN":
        return {
          ...state,
          setFormKecukupan: {
            ...state.setFormKecukupan,
            [action.formType]: action.formValue,
          },
        };
      case "ADD_KETERSEDIAAN":
        return {
          ...state,
          setFormKetersediaan: {
            ...state.setFormKetersediaan,
            [action.formType]: action.formValue,
          },
        };
      case "ADD_KONDISI":
        return {
          ...state,
          setFormKondisi: {
            ...state.setFormKondisi,
            [action.formType]: action.formValue,
          },
        };
      default:
        return state;
    }
}

export default masterBobotReducer;
