const initialState = {
  dataComboProv: [],
  dataComboJenisVariabel: [],

};

const  comboReducer = (state = initialState, action) =>{

  switch (action.type) {
    case "DATA_CMB_PROV":
       return { ...state, dataComboProv: action.payload };
    case "DATA_CMB_JENIS_VARIABEL":
      return { ...state, dataComboJenisVariabel:action.payload };
    default:
      return state;
  }
}

export default comboReducer;;
