const initialState = {
  SetDashboardListReducer : []
}

const dashboardReducer = (state = initialState, action) => {
  switch (action.type) {
    case "DASHBOARD_LIST":
      return {
        ...state,
        SetDashboardListReducer: action.payload
      };

    default:
      return state;
  }
}

export default dashboardReducer;
