import axios from 'axios';
import Swal from 'sweetalert2';
const baseAPIUrl = process.env.REACT_APP_ROOT_API;
const headers = {
        "Content-type":"application/json",
    };
export const http = axios.create({
    baseURL : baseAPIUrl,
     headers: {
    'Accept': 'application/json',
    'Content-Type': 'application/json',
  },
});


// Add a request interceptor
axios.interceptors.request.use(function (config) {
  // Do something before request is sent, like we're inserting a timeout for only requests with a particular baseURL
  if (config.baseURL === '') {
    config.timeout = 4000
  } else {
    return config
  }
  console.log (config)
    return config;
  }, function (error) {
  // Do something with request error
  return Promise.reject(error);
});

// Add a response interceptor
http.interceptors.response.use(
    (response) => {
        return response;
  },
    (error) => {
        if (error.response) {
            const { data } = error.response;
         if (error.request) {
            // Swal.fire('Error Connection', `There is problem connecting to server. Please check your connection!`, 'error').then(() => window.location = '/')
            // localStorage.clear();
            return Promise.reject({
                message: 'There is problem connecting to server. Please check your connection!',
            });
        } else {
            return Promise.reject({ message: error.message });
        }
    }
  }
);

export default http

