const initialState = {
  GetDataJabatan: [],
  modalJabatan: false,
  SetFormJabatan: {
    IdJabatan: 0,
    Kode: "",
    Nama: "",
  },
};


const masterJabatanReducer = (state = initialState, action) => {
  switch (action.type) {
    case "GET_LIST_JABATAN":
      return { ...state, GetDataJabatan: action.payload };
    case "MODAL_JABATAN":
      return { ...state, modalJabatan: action.payload };
    case "RESET_FORM_JABATAN":
      return { ...state, SetFormJabatan: initialState.SetFormJabatan };
    case "ADD_JABATAN":
      return {
        ...state,
        SetFormJabatan: {
          ...state.SetFormJabatan,
          [action.formType]: action.formValue,
        },
      };

    default:
      return state;
  }
}

export default masterJabatanReducer;
