const initialState = {
    formLogin: {
        UsernameOrEmail: '',
        Password: '',
    }
}

const loginReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'login':
            return {
                ...state,
                formLogin: {
                    ...state.formLogin,
                    [action.formType]: action.formValue
                }
            }
        case 'resetLogin':
            return { ...state, formLogin: initialState.formLogin }
        default:
            return state
    }
}

export default loginReducer;
