
const initialState = {
  GetKabupaten: [],
  modalKab:false,
  GetIdProvinsi: {
    IdProvinsi: 0
  },
  formKabupaten: {
    IdKabupaten:0,
    IdProvinsi: 0,
    Kode: "",
    Nama: "",
  },
};

const kabupatenReducer = (state =initialState, action) =>{
    switch (action.type) {
      case "GET_KABUPATENS":
        return { ...state, GetKabupaten: action.payload };
      case "GET_ID_PROV":
        return {
          ...state,
          GetIdProvinsi: {
            ...state,
            [action.formType]: action.formValue,
          },
        };
      case "RESET_FORM_KAB":
        return { ...state, formKabupaten: initialState.formKabupaten };
      case "MODAL_KAB":
        return { ...state, modalKab:action.payload};
      case "ADD_KAB":
        return {
          ...state,
          formKabupaten: {
            ...state.formKabupaten,
            [action.formType]: action.formValue,
          },
        };
      default:
        return state;
    }
}

export default kabupatenReducer
