const initialState = {
  GetJenisVariabels: [],
  GetVariabels:[],
  modalVariabel: false,
  formVariabel: {
    IdUser: 0,
    IdJenisVariabel: 0,
    Nama: "",
    IdVariabel: 0,
    Presentase: "",
    JenisVariabel: "",
  },
};

const masterVariabelReducer = (state = initialState, action) => {

  switch (action.type) {
    case "GET_CMB_JNS_VARIABEL":
      return {
        ...state,
        GetJenisVariabels: action.payload,
      };
    case "MODAL_VARIABEL":
      return { ...state, modalVariabel: action.payload };
    case "GET_VARIABELS":
      return { ...state, GetVariabels: action.payload };
    case "ADD_VARIABEL":
      return {
        ...state,
        formVariabel: {
          ...state.formVariabel,
          [action.formType]: action.formValue,
        },
      };
    case "RESET_FORM_VARIABEL":
      return { ...state, formVariabel: initialState.formVariabel };

    default:
      return state;
  }
}

export default masterVariabelReducer;
