import { combineReducers } from 'redux';

import sidebarReducer from './sidebarReducer';
import loginReducer from './loginReducer';
import dropdownReducer from "./dropdownReducer";
import kabupatenReducer from "./kabupatenReducer";
import masterVariabelReducer from "./masterVariabelReducer";
import masterBobotReducer from "./masterBobotReducer";
import masterJabatanReducer from "./masterJabatanReducer";
import dashboardReducer from "./dashboardReducer";


const reducer = combineReducers({
  sidebarReducer,
  loginReducer,
  dropdownReducer,
  kabupatenReducer,
  masterVariabelReducer,
  masterBobotReducer,
  masterJabatanReducer,
  dashboardReducer
});

export default reducer;
